// custom fonts
@font-face {
    font-family: 'IranSansWeb';
    font-display: swap;
    src: url(/fonts/IRANSansWeb.ttf) format("truetype");
}
$iran-sans: "IranSansWeb";

h1,h2,h3,h4,h5,h6,p,span,button, a, ul, li, input, textarea, blockquote, label {
    font-family: $iran-sans;
}
div {
    font-family: $iran-sans;
}
cite::before {
    font-family: $iran-sans;
}